import React, { Component } from 'react';

class Contact extends Component {
    render() {
        return (
            <div className="container">
            <div id="contactFormID" className="contactForm row">
                <h2> </h2>
                <hr/>
                <h3>    </h3>
                <form className="form-group" action="" method="POST">
                    <input id="name" name="name" className="inputInline form-control" type="text" placeholder="" required/>
                    <input id="email" name="email" className="inputInline form-control" type="email" placeholder="e-mail" required/>
                    <textarea id="message" name="message" className="form-control" placeholder="" required></textarea>
                    <input className="bannerBtn" type="submit" value=" " />
                </form>
            </div>
        </div>
        );
    }
}

export default Contact;



